import React from "react";
import {
	Row,
	Col,
	Card,
	CardImg,
	CardBody,
	CardTitle,
	CardSubtitle,
	Button,
	ButtonGroup,
	UncontrolledPopover,
	PopoverHeader,
	PopoverBody,
	UncontrolledTooltip,
} from "reactstrap";

import { truncate } from "../../helpers/helpers";

import "./Card.css";

const CardComponent = ({ movie, handleLikeDislike, handleDelete, i }) => {
	const [likes, setLikes] = React.useState(movie.likes);
	const [dislikes, setDislikes] = React.useState(movie.dislikes);
	const calculateBar = () => {
		const likesElem = document.getElementById(`likes-${i}`);
		const dislikesElem = document.getElementById(`dislikes-${i}`);

		if (!likesElem && !dislikesElem) return;
		// Simple math to calculate percentages
		const totalLikes = movie.likes + movie.dislikes;
		const percentLikes = (movie.likes / totalLikes) * 100;
		const percentDislikes = (movie.dislikes / totalLikes) * 100;
		// We need to apply the widths to our elements
		likesElem.style.width = `${!Number.isNaN(percentLikes) ? percentLikes : "50"}%`;
		dislikesElem.style.width = `${!Number.isNaN(percentDislikes) ? percentDislikes : "50"}%`;
	};
	const { poster_path, title } = movie;
	const categories = movie.categories.filter(Boolean).join(", ");

	React.useEffect(() => {
		calculateBar();
	}, [likes, dislikes]);

	return (
		<Card>
			<CardImg
				src={"https://image.tmdb.org/t/p/original/" + poster_path}
				alt={title}
				top
				width="100%"
			/>
			<CardBody>
				<Row>
					<Col>
						<div className="thumbs-icons">
							<i className="fa fa-thumbs-up"></i> &nbsp;&nbsp;{" "}
							<span id={`count-likes-${i}`}>{likes}</span>
							&nbsp; / &nbsp;
							<i className="fa fa-thumbs-down"></i> &nbsp;&nbsp;{" "}
							<span id={`count-dislikes-${i}`}>{dislikes}</span>
						</div>
						<div className="percent-bar">
							<div className="likes" id={`likes-${i}`}></div>
							<div className="dislikes" id={`dislikes-${i}`}></div>
						</div>
					</Col>
				</Row>
				<Row>
					<Col>
						{/* CATEGORIES */}
						<UncontrolledTooltip placement="top" target={`ctg-tooltip-${i}`}>
							{categories}
						</UncontrolledTooltip>
						<CardSubtitle tag="h6" className="mt-2 mb-2 text-muted card-categories">
							<span id={`ctg-tooltip-${i}`}>{categories}</span>
						</CardSubtitle>
						{/* TITLE */}
						<UncontrolledTooltip placement="top" target={`title-tooltip-${i}`}>
							{title}
						</UncontrolledTooltip>
						<CardTitle tag="h5">
							<span id={`title-tooltip-${i}`}>{title}</span>
						</CardTitle>
					</Col>
				</Row>
				<p className={`card-overview-${i}`}>
					{movie.overview ? (
						<>
							<UncontrolledPopover
								trigger="focus"
								placement="bottom"
								target={`popover-focus-${i}`}
							>
								<PopoverHeader>{movie.title}</PopoverHeader>
								<PopoverBody>{movie.overview}</PopoverBody>
							</UncontrolledPopover>
							<Button id={`popover-focus-${i}`} type="button">
								{truncate(movie.overview, 10, "...")}
							</Button>
						</>
					) : (
						"Aucune description"
					)}
				</p>
				<hr />
				<div className="card-custom-footer">
					<ButtonGroup size="sm">
						<Button
							onClick={() => {
								handleLikeDislike(movie, "like", setLikes);
								calculateBar();
							}}
							className="card-like-btn"
							title="J'aime"
						>
							<i className="fa fa-thumbs-up"></i>
						</Button>
						<Button
							onClick={() => {
								handleLikeDislike(movie, "dislike", setDislikes);
								calculateBar();
							}}
							className="card-dislike-btn"
							title="J'aime pas"
						>
							<i className="fa fa-thumbs-down"></i>
						</Button>
						<Button onClick={() => handleDelete(movie)} className="card-delete-btn">
							<i className="fa fa-trash"></i>
						</Button>
					</ButtonGroup>
				</div>
			</CardBody>
		</Card>
	);
};

export default CardComponent;
