import { axiosRequest, categoriesNames, removeCategoriesIfNoMoviesFound } from "../helpers/helpers";

import {
	SET_MOVIES,
	SET_PAGINATION,
	SET_CATEGORIES,
	SET_LOADING,
	SET_MOVIE,
	SET_NBRS_TO_DISPLAY,
	FILTER_BY_CATEGORIES,
	DELETE_MOVIE,
	UPDATE_MOVIES_LIST,
	CLEAR_FILTERED_DATA,
	UPDATE_FILTERED_BY_VALUE,
} from "../constants/action-types";

const moviesStore = "moviesStore"; // For localStorage

/**
 * Remove the films present in the trash in the movies source
 *
 * @param {Object} movies
 */
const remoteMoviesInTrashFromResults = async (results) => {
	const moviesFromTrash = JSON.parse(localStorage.getItem("trash")) || [];

	await moviesFromTrash.map((movie, i) => {
		results.map((m) => m.id === movie.id && results.splice(i, moviesFromTrash.length));
	});
	return results;
};

/**
 * Get movies
 *
 * @param {Number} page
 */
export const getMovies = (props, page) => async (dispatch) => {
	const path = `/3/movie/popular?language=fr-FR&page=${page}&include_image_language=fr,null`;

	await axiosRequest.get(path).then(async (response) => {
		const moviesFromStorage = JSON.parse(localStorage.getItem(moviesStore)) || [];
		const movies = await remoteMoviesInTrashFromResults(response.data.results);
		const pagination = response.data;
		pagination.page = page;

		movies.map((movie, i) => {
			movie.likes = 0;
			movie.dislikes = 0;
			movie.likes_dislikes_by_users_ids = [];
			// Check if we've not (dis)like(s) movie(s) in storage
			moviesFromStorage.map((m) => {
				if (m.id === movie.id) movies[i] = Object.assign(m, { movie, ...m });
			});
		});
		dispatch(getCategories(movies)); // We cannot have the categories name with "getMovies" method, he return only ids of genres so we use getGenres method
		return dispatch({
			type: SET_MOVIES,
			payload: { ...props, movies, pagination },
		});
	});
};

/**
 * Update movies
 *
 * @param {Object<Array>} movies
 */
export const updateMovies = (movies) => {
	return {
		type: UPDATE_MOVIES_LIST,
		payload: { movies },
	};
};

/**
 * Update movie
 *
 * @param {Object} page
 */
export const updateMovie = (movie) => {
	return {
		type: SET_MOVIE,
		payload: { movie },
	};
};

/**
 * Delete movies
 *
 * @param {Object} movie
 */
export const deleteMovie = (movie) => {
	return {
		type: DELETE_MOVIE,
		payload: { movie },
	};
};

/**
 *
 * @param {Object} pagination
 */
export const setPagination = (pagination) => (dispatch) => {
	setTimeout(() => dispatch(setLoading(false)), 1500);
	return {
		type: SET_PAGINATION,
		payload: { pagination },
	};
};

/**
 *
 * @param {Number} nbrsToDisplay
 */
export const setNbrsToDisplay = (nbrsToDisplay) => {
	return {
		type: SET_NBRS_TO_DISPLAY,
		payload: { nbrsToDisplay },
	};
};

/**
 *
 * @param {Object} props
 */
export const paginate = (props) => (dispatch) => {
	const { pagination } = props;
	const { page } = pagination;

	dispatch(setLoading(true));
	dispatch(getMovies(props, page));
	setTimeout(() => setPagination(pagination), 2500);

	return {
		type: SET_PAGINATION,
		payload: { pagination },
	};
};

/**
 * Update loadinf app
 *
 * @param {Boolean} value
 */
export const setLoading = (value) => {
	return {
		type: SET_LOADING,
		payload: {
			loadingApp: value,
		},
	};
};

/**
 *
 * @param {String} value
 */
export const updateFilteredByValue = (value) => {
	return {
		type: UPDATE_FILTERED_BY_VALUE,
		payload: {
			filteredBy: value,
		},
	};
};

/**
 *
 * Get categories or genres
 */
export const getCategories = (movies) => async (dispatch) => {
	const path = `/3/genre/movie/list?&language=fr-FR`;
	const response = await axiosRequest.get(path);
	const genres = response.data.genres;

	await movies.map((m) => (m.categories = categoriesNames(genres, m).filter(Boolean)));
	dispatch(updateMovies(movies));
	setTimeout(() => dispatch(setLoading(false)), 1500);
	return dispatch({
		type: SET_CATEGORIES,
		payload: { categories: genres },
	});
};

export const clearFilteredData = () => {
	return {
		type: CLEAR_FILTERED_DATA,
	};
};

/**
 *
 * Get categories or genres
 */
export const filterByCategories = (props, filteredBy) => async (dispatch) => {
	const { categories, movies } = props;
	const categorie = categories.find((c) => c.name === filteredBy);
	let filteredMovies = [];

	dispatch(setLoading(true));

	if (filteredBy === "reset") {
		dispatch(clearFilteredData());
		return dispatch(getMovies(props, 1));
	} else {
		dispatch(updateFilteredByValue(filteredBy));
		filteredMovies = movies.filter((m) => m.genre_ids.includes(categorie.id));
	}

	setTimeout(() => dispatch(setLoading(false)), 1500);

	return dispatch({
		type: FILTER_BY_CATEGORIES,
		payload: {
			filteredMovies,
			movies,
			filteredBy,
		},
	});
};

/**
 * Update categories
 *
 * @param {Object} categories
 * @param {Object} movies
 */
export const updateCategories = (categories, movies, movie) => {
	categories = removeCategoriesIfNoMoviesFound(categories, movies, movie);

	return {
		type: SET_CATEGORIES,
		payload: { categories },
	};
};
