import {
	SET_CATEGORIES,
	SET_LOADING,
	SET_MOVIE,
	SET_MOVIES,
	SET_PAGINATION,
	SET_NBRS_TO_DISPLAY,
	FILTER_BY_CATEGORIES,
	DELETE_MOVIE,
	UPDATE_MOVIES_LIST,
	CLEAR_FILTERED_DATA,
	UPDATE_FILTERED_BY_VALUE,
} from "../constants/action-types";

const initialState = {
	movies: [],
	filteredMovies: [],
	categories: [],
	loadingApp: true,
	pagination: {
		page: 1,
		total_pages: 500,
		nbrsToDisplay: 12,
		nbrsToDisplayList: [4, 8, 12],
		filteredBy: "",
	},
};

// Each action will be called manually
const rootReducer = (state = initialState, action) => {
	if (action.type === SET_MOVIES) {
		const movies = action.payload.movies;

		return {
			...state,
			movies,
			filteredMovies: movies, // If we paginate also
			pagination: {
				...state.pagination,
				...action.payload.pagination,
			},
		};
	} else if (action.type === UPDATE_MOVIES_LIST) {
		const movies = state.movies;

		return { ...state, movies };
	} else if (action.type === SET_MOVIE) {
		const movie = action.payload.movie;
		const index = state.movies.findIndex((m) => m.id === movie.id);
		const movies = state.movies;
		movies[index] = movie;

		return { ...state, movies };
	} else if (action.type === DELETE_MOVIE) {
		const movie = action.payload.movie;
		const movies = state.movies.filter((m) => m.id !== movie.id);

		return { ...state, movies };
	} else if (action.type === SET_CATEGORIES) {
		const categories = action.payload.categories;

		return { ...state, categories };
	} else if (action.type === SET_LOADING) {
		return Object.assign({}, state, {
			loadingApp: action.payload.loadingApp,
		});
	} else if (action.type === SET_PAGINATION) {
		return Object.assign({}, state, {
			pagination: {
				...state.pagination,
				...action.payload.pagination,
			},
		});
	} else if (action.type === FILTER_BY_CATEGORIES) {
		const filteredMovies = action.payload.filteredMovies;
		const movies = action.payload.movies;
		const filteredBy = action.payload.filteredBy;

		return {
			...state,
			filteredMovies,
			movies,
			pagination: {
				...state.pagination,
				filteredBy,
			},
		};
	} else if (action.type === UPDATE_FILTERED_BY_VALUE) {
		const filteredBy = action.payload.filteredBy;

		return {
			...state,
			pagination: {
				...state.pagination,
				filteredBy,
			},
		};
	} else if (action.type === CLEAR_FILTERED_DATA) {
		return {
			...state,
			filteredMovies: [],
			pagination: {
				...state.pagination,
				filteredBy: "",
			},
		};
	} else if (action.type === SET_NBRS_TO_DISPLAY) {
		return {
			...state,
			pagination: {
				...state.pagination,
				nbrsToDisplay: action.payload.nbrsToDisplay,
			},
		};
	}

	return state;
};

export default rootReducer;
