import React from "react";
import { Container, Row, Col } from "reactstrap";
import { useDispatch, useSelector, connect } from "react-redux";
import Swal from "sweetalert";
import { css } from "@emotion/core";
import ClipLoader from "react-spinners/ClipLoader";

import {
	getMovies,
	updateMovie,
	updateCategories,
	setNbrsToDisplay,
	getCategories,
	deleteMovie,
} from "../../actions/index.js";
import Pagination from "../Pagination/Pagination.jsx";
import Card from "../Card/Card.jsx";
import { setOrGetUserId, updateParam } from "../../helpers/helpers.js";

import "./Movies.css";

const override = css`
	display: block;
	margin: 0 auto;
	border-color: red;
	margin-top: 100px;
	margin-bottom: 100px;
`;

const MoviesComponent = (props) => {
	const movies = useSelector((state) => state.movies);
	const filteredMovies = useSelector((state) => state.filteredMovies);
	const categories = useSelector((state) => state.categories);
	const loadingApp = useSelector((state) => state.loadingApp);
	const { getMovies, updateMovie, deleteMovie, setNbrsToDisplay } = props;
	const dispatch = useDispatch();

	const moviesStore = "moviesStore";

	React.useEffect(() => {
		const page = updateParam(props, "page", 1);
		const nbrs = updateParam(props, "nbrs-to-display", 12);

		updateParam(props, "filtered-by", "");
		setOrGetUserId();
		getMovies(props, page);
		setNbrsToDisplay(nbrs);
	}, []);

	/**
	 *
	 * @param {Object} movie
	 */
	const handleDelete = (movie) => {
		Swal({
			title: "Etes-vous sûr de vouloir effectuer cette action ?",
			text: "",
			icon: "warning",
			buttons: true,
			dangerMode: true,
		}).then((ok) => {
			if (ok) {
				const moviesFromTrash = JSON.parse(localStorage.getItem("trash")) || [];
				const moviesFromState = movies;
				const indexFromState = moviesFromState.findIndex((m) => m.id === movie.id);
				const moviesFromStorage = JSON.parse(localStorage.getItem(moviesStore)) || [];
				const indexFromStorage = moviesFromStorage.findIndex((m) => m.id === movie.id);

				moviesFromTrash.push(movie);
				moviesFromStorage.splice(indexFromStorage, 1);
				moviesFromState.splice(indexFromState, 1);
				dispatch(updateCategories(categories, movies, movie));
				// setTimeout(() => dispatch(updateCategories(categories, movies, movie)), 300);
				dispatch(deleteMovie(movie));
				localStorage.setItem(moviesStore, JSON.stringify(moviesFromStorage)); // Update storage
				localStorage.setItem("trash", JSON.stringify(moviesFromTrash)); // Set deleted movie to trash
				return Swal("Effacé avec succès !", { icon: "success" });
			}
		});
	};
	/**
	 *
	 * @param {Object} movie
	 * @param {String} action=like|dislike
	 * @param {Function} cb=like|dislike
	 */
	const handleLikeDislike = (movie, action, cb) => {
		const userId = setOrGetUserId(true);
		const moviesFromStorage = JSON.parse(localStorage.getItem(moviesStore)) || [];
		const movieIndex = moviesFromStorage.findIndex((m) => m.id === movie.id);
		const alreadyInStorage = moviesFromStorage[movieIndex];
		const actionsNameKey = "likes_dislikes_by_users_ids";
		const alternateKey$1 = action === "like" ? "dislike" : "like";
		// Get array actions
		const moviesActions = alreadyInStorage ? alreadyInStorage[actionsNameKey] : [];
		// Find actions for curent user
		const users = moviesActions.filter((u) => u.userId === userId);
		// Check if user has not alredy like or dislike
		if (alreadyInStorage) {
			if (users.length > 0) {
				const user = users[0];
				// If user actions includes current action, delete it
				moviesFromStorage.splice(movieIndex, 1);

				if (user.actions.includes(action) && movie[`${action}s`] > 0) {
					user.actions = user.actions.filter((v) => v === action);
					movie[`${action}s`] -= 1;
				}
				if (user.actions.includes(alternateKey$1) && movie[`${alternateKey$1}s`] > 0) {
					user.actions = user.actions.filter((v) => v === alternateKey$1);
					movie[`${alternateKey$1}s`] -= 1;
				}

				user.actions = user.actions.filter((v) => v === action);
				movie[actionsNameKey] = [...user.actions, action];
			}
		} else {
			movie[`${action}s`] += 1;
			// New like or dislike for current user, so init with push
			movie[actionsNameKey].push({ userId, actions: [action] });
			moviesFromStorage.push(movie);
		}

		cb(movie[`${action}s`]);
		localStorage.setItem(moviesStore, JSON.stringify(moviesFromStorage)); // Update storage
		dispatch(updateMovie(movie)); // Update state
	};
	const dupPagination = () => {
		return (
			<>
				<hr />
				<Pagination {...props} />
				<hr />
			</>
		);
	};

	if (loadingApp) return <ClipLoader css={override} size={150} loading={loadingApp} />;

	const {
		pagination: { nbrsToDisplay, filteredBy },
	} = props;
	let dataMovies = filteredMovies.length > 0 ? filteredMovies : movies;

	if (filteredBy && dataMovies.length > 0) {
		dataMovies = dataMovies.filter((m) => m.categories.includes(filteredBy));
	}

	return (
		<Container className="movies-container">
			{dupPagination()}
			{dataMovies.length > 0 ? (
				<Row>
					{dataMovies.map((movie, i) => {
						if (i + 1 <= nbrsToDisplay) {
							return (
								<Col md="4" key={i}>
									<Card
										i={i}
										movie={movie}
										handleDelete={handleDelete}
										handleLikeDislike={handleLikeDislike}
									/>
								</Col>
							);
						}
						return <React.Fragment key={i} />;
					})}
				</Row>
			) : (
				<div className="nothing-to-display">Aucun contenu à afficher...</div>
			)}
			{dupPagination()}
		</Container>
	);
};

// I use selector also directly in component
const mapStateToProps = (state) => ({
	pagination: state.pagination,
});

const mapDispatchToProps = {
	getMovies,
	getCategories,
	updateMovie,
	updateCategories,
	setNbrsToDisplay,
	deleteMovie,
};

export default connect(mapStateToProps, mapDispatchToProps)(MoviesComponent);
