import axios from "axios";
import qs from "qs";

const replace = (str) => str.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, "");

export const truncate = (str, limit, after) => {
	let content = replace(str);
	content = content.split(" ").slice(0, limit);
	content = content.join(" ") + (after ? after : "");

	return content;
};

