/**
 * Set or update query Params from url
 *
 * @param {Object} props
 * @param {String} name
 * @param {String} val
 */
export const updateParam = (props, name, val) => {
	const { location } = props;
	const queryParams = qs.parse(location.search, { ignoreQueryPrefix: true });
	const newQueries = { ...queryParams, [name]: val };

	if (val === "") delete newQueries[name];
	props.history.push({ search: qs.stringify(newQueries) });
	return queryParams[name] || newQueries[name] || val;
};

