/**
 * If all the movies in a category are deleted, that category should no longer appear.
 *
 * @param {Object} movie
 */
export const removeCategoriesIfNoMoviesFound = (categories, movies) => {
	movies.map((m) => {
		m.categories.map((mc) => {
			if (!categories.find((c) => c.name === mc)) {
				const i = categories.findIndex((c) => c.name === mc);

				categories.splice(i, 1);
			}
		});
	});

	return categories;
};

/**
 * Return an integer range within [min, min + total) of given length centered
 * around the current page number.
 */
export const getPagingRange = (current, { min = 1, total = 20, length = 5 } = {}) => {
	if (length > total) length = total;

	let start = current - Math.floor(length / 2);
	start = Math.max(start, min);
	start = Math.min(start, min + total - length);

	return Array.from({ length: length }, (el, i) => start + i);
};

export const categoriesNames = (categories, movie) => {
	const categoriesByName = categories.map((c) => {
		if (movie) {
			return movie.genre_ids.find((elem) => elem === c.id) && c.name;
		} else {
			return c.name;
		}
	});

	return categoriesByName;
};
